import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
import Vuex from 'vuex'
export default {
	data() {
		return {
			value: 0,
			updateState: true,
		};
	},
	computed: {
		...Vuex.mapState(['detail', 'audio', 'paused', 'mode', 'start', 'total', 'history', 'musicIndex'])
	},
	methods: {
		...Vuex.mapMutations(["play", "pause", 'setMode', 'setTime', 'setIndex', 'setSrc', 'setDetail']),
		plan() {
			/* uni.setNavigationBarTitle({
				title:this.detail.songs[0].name
			}); */
			// 当前播放时间
			let min = Math.floor(this.audio.currentTime / 60);
			min = min.toString().length === 1 ? ("0" + min): min;
			let sec = Math.round(this.audio.currentTime % 60);
			sec = sec.toString().length === 1 ? ("0" + sec): sec;
			// 总时间
			let zmin = Math.floor(this.audio.duration / 60);
			zmin = zmin.toString().length === 1 ? ("0" + zmin): zmin;
			let zsec = Math.round(this.audio.duration % 60);
			zsec = zsec.toString().length === 1 ? ("0" + zsec): zsec;
			
			this.setTime([`${min}:${sec}`, `${zmin}:${zsec}`])
			if (this.updateState) {
				this.value = this.audio.currentTime / this.audio.duration * 100
			}
			
		},
		change(e) {
			let t = this.audio.duration*(e.detail.value/100);
			this.audio.seek(t)
			this.updateState = true;
			// 播放到了多长时间
			let min = Math.floor(t / 60);
			min = min.toString().length === 1 ? ("0" + min): min;
			let sec = Math.round(t % 60);
			sec = sec.toString().length === 1 ? ("0" + sec): sec;
			let s = `${min}:${sec}`
			
			
		},
		changing() {
			this.updateState = false;
		},
		download(){
			// downloadFile 相当于临时缓存
			uni.downloadFile({
				url: this.audio.src,
				success: (res) => {
					//console.log(res)
					
					// 浏览器下载方式
					var a = document.createElement('a');
					a.href = res.tempFilePath;
					a.download = this.detail.songs[0].name + '.mp3'
					a.click()
					
					// 手机app下载方式 永久保存
					/* uni.saveFile({
						tempFilePath: res.tempFilePath
					}) */
				}
			})
		},
		next() {
			this.setIndex(1)
			let src = this.history[this.musicIndex].src;
			let detail = this.history[this.musicIndex].detail;
			this.setSrc(src);
			this.setDetail(detail)
			this.paused ? this.play() : this.pause()
		},
		prev() {
			this.setIndex(-1)
			let src = this.history[this.musicIndex].src;
			let detail = this.history[this.musicIndex].detail;
			this.setSrc(src);
			this.setDetail(detail)
			this.paused ? this.play() : this.pause()
		}
	},
	mounted() {
		uni.setNavigationBarTitle({
			title:this.detail.songs[0].name
		});
		// 当音乐时间改变的事件
		this.audio.onTimeUpdate(this.plan);
		this.audio.onPlay(() => {
			uni.setNavigationBarTitle({
				title:this.detail.songs[0].name
			})
		});
	},
	components: {
		cmdIcon
	}
}
